.nav {
  position: relative;
  z-index: 5; }

.nav__mobile {
  position: absolute;
  padding: 15px 0;
  width: 30px;
  cursor: pointer;
  transition: .2s; }
  .nav__mobile:hover .gamburger {
    background-color: #6ECF53; }
  .nav__mobile .gamburger {
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #494D4B; }
    .nav__mobile .gamburger::before, .nav__mobile .gamburger::after {
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-color: inherit;
      transition: width .2s .4s, top .2s .2s, transform .2s; }
    .nav__mobile .gamburger::before {
      top: -10px; }
    .nav__mobile .gamburger::after {
      top: 10px;
      width: 50%; }

.nav__desktop {
  position: absolute;
  left: 50%;
  min-width: 1024px;
  transform: translateX(-50%) translateY(-50%);
  font-size: 18px;
  color: #494D4B;
  box-shadow: 0px 1px 1px 1px #00000020;
  border-radius: 3px;
  background-color: #fff;
  z-index: 5; }

.desktop__items {
