  margin: 0;
  list-style: none; }

.desktop__item {
  display: inline-block;
  padding: 20px 0;
  color: #494D4B;
  text-align: center;
  transition: .2s; }
  .desktop__item:not(:first-of-type) .link {
    border-left: 1px solid #DDDDDD; }
  .desktop__item:hover > .link {
    color: #6ECF53; }
  .desktop__item:hover .desktop__item-parent {
    visibility: visible;
    opacity: 1;
    z-index: 5; }
  .desktop__item .link {
    padding: 0 35px;
    color: #494D4B;
    transition: .2s; }
  .desktop__item .desktop__item-parent {
    position: absolute;
    left: 0;
    padding: 50px 150px 30px 40px;
    width: 100%;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    transition: .3s; }
    .desktop__item .desktop__item-parent .desktop__item {
      padding: 0;
      width: 30%;
      text-align: left;
      color: #494D4B;
      transition: .2s; }
      .desktop__item .desktop__item-parent .desktop__item .link {
